// Import necessary WordPress packages

import {registerPlugin} from '@wordpress/plugins';
import {PluginSidebar} from '@wordpress/editor';
import {
    Button,
    Flex,
    FlexItem,
    ToggleControl,
    SelectControl,
    PanelBody,
    TextControl,
    __experimentalToggleGroupControl as ToggleGroupControl,
    __experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import {useDispatch} from '@wordpress/data';
import {__} from '@wordpress/i18n';
import {Fragment, useEffect, useState} from '@wordpress/element';
import React from "react";
import apiFetch from '@wordpress/api-fetch';
import defaultAttributes from "./default-attributes";
import AccordionIconsStylesPanel from "../accordion/editor/styles/accordion-icon";
import QaIconsStylesPanel from "../accordion/editor/styles/qa-icons";
import AccordionStylesPanel from "../accordion/editor/styles/accordion";
import AccordionHeadStylesPanel from "../accordion/editor/styles/accordion-head";
import AccordionBodyStylesPanel from "../accordion/editor/styles/accordion-body";
import './editor.scss';
import tags from "../tags";
import AccordionDefaultIcon from "../components/icons/accordion-default";

const Options = () => {
    const [defaultStyles, setDefaultStyles] = useState(defaultAttributes); // Current settings
    const {createSuccessNotice} = useDispatch('core/notices');

    // Load saved settings on component mount
    useEffect(() => {
        loadSidebarSettings();
    }, []);

    // Load settings from the WordPress database (wp_options)
    const loadSidebarSettings = async () => {
        try {
            const data = await apiFetch({path: '/wp/v2/settings'});
            const savedSettings = data.aab_settings_defaults
                ? JSON.parse(data.aab_settings_defaults)
                : null;

            if (!savedSettings) {
                // Save defaultAttributes to database for the first time
                await apiFetch({
                    path: '/wp/v2/settings',
                    method: 'POST',
                    data: {
                        aab_settings_defaults: JSON.stringify(defaultAttributes),
                    },
                });
                setDefaultStyles(defaultAttributes);
            } else {
                setDefaultStyles({...defaultAttributes, ...savedSettings});
            }
        } catch (error) {
            console.error('Error loading sidebar settings:', error);
        }
    };

    // Change a specific setting
    const changeSidebarSetting = (updates) => {
        setDefaultStyles((prevState) => ({
            ...prevState,
            ...updates,
        }));
    };
    // Reset to default settings
    const resetSidebarSettings = () => {
        changeSidebarSetting(defaultAttributes);
    };

    // Save settings to the WordPress database
    const saveSidebarSettings = async () => {
        const settingsToSave = {...defaultStyles};
        try {
            await apiFetch({
                path: '/wp/v2/settings',
                method: 'POST',
                data: {aab_settings_defaults: JSON.stringify(settingsToSave)},
            });
            createSuccessNotice(__('AAB Global styles saved successfully!', 'advanced-accordion-block'),
                {type: 'snackbar'}
            );
            // Reload the page after a short delay to show the notice
            setTimeout(() => {
                window.location.reload();
            }, 800); // delay 1s for snackbar to show
        } catch (error) {
            console.error('Error saving AAB Global styles:', error);
            createSuccessNotice(__('Failed to save AAB Global styles.', 'advanced-accordion-block'),
                {type: 'snackbar'}
            );
        }
    };

    const prop = {
        attributes: defaultStyles,
        setAttributes: changeSidebarSetting
    };

    // Check if aab_premium is true
    const aab_premium = aagb_local_object.licensing;
    return (
        <PluginSidebar
            title={__('AAB Global Style', 'advanced-accordion-block')}
        >
            <PanelBody
                title={__('Settings', 'advanced-accordion-block')}
                initialOpen={false}
                className={aab_premium ? "" : "aab-pro-element"}
            >
                {aab_premium && (
                    <ToggleGroupControl
                        label={__('Accordion Layout', 'advanced-accordion-block')}
                        value={defaultStyles.QaStyle ? 'QaLayout' : 'default'}
                        onChange={(value) => changeSidebarSetting({QaStyle: value === 'QaLayout'})}
                        isBlock
                    >
                        <ToggleGroupControlOption value="default" label={__('Default', 'advanced-accordion-block')}/>
                        <ToggleGroupControlOption value="QaLayout" label={__('Q A', 'advanced-accordion-block')}/>
                    </ToggleGroupControl>
                )}

                {defaultStyles.QaStyle && (
                    <>
                        <TextControl
                            label={__(
                                'Set Q Icon Text',
                                'advanced-accordion-block'
                            )}
                            value={defaultStyles.qIconText}
                            onChange={(qIconText) => changeSidebarSetting({qIconText})}
                        />
                        <TextControl
                            label={__(
                                'Set A Icon Text',
                                'advanced-accordion-block'
                            )}
                            value={defaultStyles.aIconText}
                            onChange={(aIconText) => changeSidebarSetting({aIconText})}
                        />
                    </>
                )}
                <SelectControl
                    label={__(
                        'Select Heading Tag',
                        'advanced-accordion-block'
                    )}
                    options={tags}
                    onChange={(headingTag) => changeSidebarSetting({headingTag})}
                    value={defaultStyles.headingTag}
                />
                <ToggleControl
                    label={__(
                        'Anchor Link',
                        'advanced-accordion-block'
                    )}
                    disabled={!aab_premium}
                    checked={defaultStyles.anchorLinkShow && aab_premium}
                    onChange={(anchorLinkShow) => changeSidebarSetting({anchorLinkShow})}
                />
                <ToggleControl
                    label={__(
                        'FAQ Schema',
                        'advanced-accordion-block'
                    )}
                    checked={defaultStyles.faqSchema}
                    onChange={(faqSchema) => changeSidebarSetting({faqSchema})}
                />
            </PanelBody>
            <QaIconsStylesPanel {...prop} />
            <AccordionStylesPanel {...prop} />
            <AccordionHeadStylesPanel {...prop} />
            <AccordionIconsStylesPanel {...prop} />
            <AccordionBodyStylesPanel {...prop} />
            <p className="custom-css-notice " style={{padding: '10px 15px 0 15px'}}>
                {__('(Make your desired changes in the global styles, then click ‘Save’ to apply them across your site.)', 'advanced-accordion-block')}
            </p>
            <Flex className='aab-default-sidebar-action' justify="end">
                <FlexItem>
                    <Button className='action-red' onClick={resetSidebarSettings}>Reset</Button>
                </FlexItem>
                <FlexItem>
                    <Button className='action-save' variant='primary' onClick={saveSidebarSettings}>Save</Button>
                </FlexItem>
            </Flex>

        </PluginSidebar>
    );
};

// Register the plugin
registerPlugin('accordion-default', {
    render: Options,
    icon: AccordionDefaultIcon,
});
