import React from "react";
import {
  __experimentalBorderControl as BorderControl,
  PanelBody,
  RangeControl,
  __experimentalToolsPanel as ToolsPanel,
  __experimentalToolsPanelItem as ToolsPanelItem
} from '@wordpress/components';
import {__experimentalBorderRadiusControl as BorderRadiusControl} from '@wordpress/block-editor';
import { __ } from "@wordpress/i18n";
import AabColorPicker from "../../../components/aab-colorpicker";
import colors from "../../../colors";
import {createAttrGetter} from "../../../utils/global-settings";

export default function AccordionIconsStylesPanel({ attributes, setAttributes }) {
  const { showIcon, iconBorder, iconBorderRadius } = attributes;

  const getAttrDeep = createAttrGetter(attributes);
  const iconColor = getAttrDeep('iconColor') ?? null;
  const iconFontSize = getAttrDeep('iconFontSize') ?? null;
  const iconBackground = getAttrDeep('iconBackground') ?? null;

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accordion Icon', 'advanced-accordion-block')}
    >
      {!showIcon && <p>To change icon style you must select Show Icon in settings tab.</p>}

      {showIcon && (
        <>
          <AabColorPicker
            label={__('Color', 'advanced-accordion-block')}
            value={iconColor}
            onChange={(iconColor) => setAttributes({iconColor})}
            bgValue={iconBackground}
            onBgChange={(iconBackground) => setAttributes({iconBackground})}
          />
          <RangeControl
            label="Font Size"
            value={iconFontSize}
            onChange={(value) =>
              setAttributes({iconFontSize: value})
            }
            min={20}
            max={50}
          />
          <ToolsPanel
            label={__('Border', 'advanced-accordion-block')}
            className='aab-component-toolpanel'
            resetAll={() => {
              setAttributes({
                iconBorder: {
                  "style": "none",
                  "width": "0px"
                }
              })
              setAttributes({iconBorderRadius: ''});
            }}
          >
            <ToolsPanelItem
              hasValue={() => iconBorder?.width !== "0px" && !!iconBorder?.width}
              label={__('Border', 'advanced-accordion-block')}
              onDeselect={() =>
                setAttributes({
                  iconBorder: {
                    "style": "none",
                    "width": "0px"
                  }
                })
              }
            >
              <BorderControl
                colors={colors}
                value={iconBorder}
                onChange={(value) => {
                  setAttributes({iconBorder: value})
                }}
                withSlider={true}
              />
            </ToolsPanelItem>
            <ToolsPanelItem
              hasValue={() => !!iconBorderRadius}
              label={__('Border Radius', 'advanced-accordion-block')}
              onDeselect={() => setAttributes({iconBorderRadius: ''})}
            >
              <BorderRadiusControl
                values={iconBorderRadius}
                onChange={(value) => {
                  setAttributes({iconBorderRadius: value})
                }}
              />
            </ToolsPanelItem>
          </ToolsPanel>
        </>
      )}
    </PanelBody>
  );
}