import React from "react";
import { PanelBody, __experimentalBorderBoxControl as BorderBoxControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import AabColorPicker from "../../../components/aab-colorpicker";
import colors from "../../../colors";
import {createAttrGetter} from "../../../utils/global-settings";

export default function AccordionHeadStylesPanel({ attributes, setAttributes }) {
  const { QaStyle } = attributes;
  const getAttrDeep = createAttrGetter(attributes);
  const headerBg = getAttrDeep('headerBg') ?? null;
  const headingBorder = getAttrDeep('headingBorder') ?? null;
  const subheadingColor = getAttrDeep('subheadingColor') ?? null;
  const headingColor = getAttrDeep('headingColor') ?? null;
  const aab_premium = aagb_local_object.licensing;

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accordion Head', 'advanced-accordion-block')}
    >
      <AabColorPicker
        label='Header Color'
        value={headingColor}
        onChange={(headingColor) => setAttributes({ headingColor })}
        bgValue={headerBg}
        onBgChange={(headerBg) => setAttributes({ headerBg })}
      />
      {aab_premium && (
        <AabColorPicker
          label={__('Sub Heading Color', 'advanced-accordion-block')}
          value={subheadingColor}
          onChange={(subheadingColor) => setAttributes({ subheadingColor })}
        />
      )}
      {!QaStyle && (
        <BorderBoxControl
          colors={colors}
          label={__('Set Heading Border', 'advanced-accordion-block')}
          onChange={headingBorder => setAttributes({ headingBorder })}
          value={headingBorder}
        />
      )}
    </PanelBody>
  );
}